.container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
}

.subcontainer{
    position: relative;
    width: 200px;
    height: 200px;
}

.ring{
    width: 100%;
    height: 100%;
    border-left: 4px solid #818181;
    border-radius: 50%;
    animation: rotate 1s linear infinite;
}

.text{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 200px;
    font-size: 22px;
    font-weight: bold;
    color: #818181;
}

@keyframes rotate {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}